import {createRouter, createWebHistory} from 'vue-router'

import MyAbout from '../views/about'
import MyHome  from '../views/home'
import MyContact  from '../views/contact'
import StyleTransfer  from '../views/styletransfer'
import MyPost1  from '../views/posts/post1'
const routes = [
    {
        path: '/about',
        name: 'about',
        component: MyAbout
    },
    {
        path: '/',
        name: 'home',
        component: MyHome
    },
    {
        path: '/contact',
        name: 'contact',
        component: MyContact
    },
    {
        path: '/styleT',
        name: 'styletransfer',
        component: StyleTransfer
    },
    {
        path: '/post1',
        name: 'MyPost1',
        component: MyPost1
    },

]

const router = createRouter({
    history: createWebHistory(process.env.
        BASE_URL),
        routes
})

export default router