<script>
import WebHeader from '../components/header.vue'
import MyFooter from '../components/footer.vue'
import axios from 'axios'

export default {
    name: 'MyContact',
    components:{
        WebHeader,
        MyFooter,
    },
    data() {
      return {
        selectedFile : null
      }
    },
    methods: {
      OnFileSelect(e) {
        this.selectedFile = e.target.files[0]
        console.log(this.selectedFile)
        this.$refs.content.src = URL.createObjectURL(this.selectedFile)
      },
      OnUpload() {
        const fd = new FormData()
        fd.append("image", this.selectedFile, this.selectedFile.name)
        axios.post("https://petstore.swagger.io/v2/store/order", fd)
        .then (res => {
          console.log(res)
        })

      }
    }
    
}
